@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;700&display=swap');
body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.header {
    width: 100%;
    height: 80px;
    background-color: transparent;
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
}

.logo {
    width: 45%;
    height: 80px;
}

.logo img {
    width: 60px;
    margin-top: 1rem;
    margin-left: 3rem;
}

.navigation-menu {
    width: 55%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    /* margin: 0; */
    margin-right: 2rem;
    padding: 0;
}

.navigation-menu li {
    list-style: none;
    margin: 0 0.5rem;
}

.navigation-menu a {
    text-decoration: none;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    color: #fff;
}

.intro {
    background-color: #121117;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.about-details {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 0 3rem 0;
    background-image: linear-gradient(to right, #391a3f 1.0%, #121117 66.6%);
}

.about-section p {
    margin: 0;
}

.about-text-one {
    font-size: 16px;
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    margin-top: 7rem;
    margin-left: 3rem;
}

.about-text-two {
    font-size: 42px;
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 700;
    margin: 1rem 0 0 3rem;
}

.about-text-three {
    font-size: 16px;
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    line-height: 1.75rem;
    margin-bottom: 2rem;
    margin-left: 3rem;
}

.home-btn {
    margin-left: 3rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}

.demo-btn {
    width: 170px;
    height: 50px;
    margin-right: 1.5rem;
    border-radius: 5px;
    border: none;
    background-color: rgb(243, 206, 39);
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 13px;
    font-weight: bold;
}

.whitepaper-btn {
    width: 150px;
    height: 50px;
    border-radius: 5px;
    border: none;
    background-color: rgb(52, 147, 255);
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 13px;
    font-weight: bold;
}

.far {
    margin-right: 0.7rem;
    font-size: 1rem;
    font-weight: bold;
}

.bike {
    width: 50%;
    padding: 3rem 0;
}

.bike img {
    width: 100%;
    height: 100%;
}

.contract-address {
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 25px;
    margin-top: 30px;
    margin-left: 3rem;
}

.contract-address p {
    margin: 0;
}

.contract-address .address {
    font-weight: bold;
}

.contract-address .tooltip {
    font-size: 20px;
    visibility: hidden;
    width: 150px;
    background-color: #311836;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 10px 0;
    position: absolute;
    cursor: pointer;
    margin-top: 0.5rem;
    border: 1px solid rgb(71, 71, 71);
}

.contract-address:hover .tooltip {
    visibility: visible;
}

.fal {
    margin-left: 1rem;
}

.video {
    overflow: hidden;
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
}

.react-player {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.features {
    width: 100%;
    background-image: url(../src/assets/bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
}

.features-text {
    margin: 0;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 1.5rem;
}

.features-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 2rem 2rem 2rem;
}

.bike-two {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    /* width: 100%; */
    /* max-width: 170px; */
}

.bike-two img {
    width: 80%;
}

.features-details-text {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    color: #fff;
    margin-top: 3rem;
}

.features-details-one, .features-details-two, .features-details-three {
    margin: 0 2rem;
}

.features-details-one :nth-child(1), .features-details-two :nth-child(1), .features-details-three :nth-child(1) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    margin: 0;
    text-align: center
}

.features-details-one :nth-child(2), .features-details-two :nth-child(2), .features-details-three :nth-child(2) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    margin-top: 17px;
    text-align: center;
    line-height: 30px;
}

.marketplace {
    background-image: linear-gradient(to right, #1f2937, #4c1d95);
    display: flex;
    flex-direction: column;
    padding: 0 3rem 3.5rem 3rem;
}

.marketplace-text-one {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: 700;
    padding-top: 4rem;
    margin: 0;
    text-align: center;
    color: #fff;
    word-wrap: break-word;
}

.marketplace-text-two {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    margin: 0;
    text-align: center;
    color: #fff;
    padding: 0 1rem 2rem 1rem;
}

.marketplace-items {
    /* padding: 0 1.5rem; */
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, minmax(180px, 279px));
    column-gap: 12px;
    justify-content: center;
}

.dirtbike {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.dirtbike-one {
    padding: 2rem 1.5rem;
    background-image: linear-gradient( 180deg, rgba(42, 45, 58, 0) 0%, #005A70 100%);
    margin: 0 0.5rem 2.5rem 0.5rem;
    border-radius: 4px;
}

.dirtbike-one img {
    width: 100%;
    max-width: 170px;
}

.dirtbike-text {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    font-weight: 700;
    margin: 1.5rem 0 0 0;
    color: #fff;
}

.dirtbike-rating {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0.3rem 0 0 0;
    color: #fff;
}

.dirtbike-rating span {
    font-weight: 700;
}

.dirtbike-rating p {
    margin: 0;
}

.dirtbike-likes {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}

.dirtbike-likes img {
    width: 15px;
    height: 13px;
    margin-right: 6px;
}

.star {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 5px;
}

.star img {
    width: 18px;
    height: 18px;
}

.scramble {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 3rem;
}

.scramble-one {
    padding: 2rem 1.5rem;
    background-image: linear-gradient( 180deg, rgba(42, 45, 58, 0) 0%, #005A70 100%);
    margin: 0 1rem;
    border-radius: 4px;
}

.scramble-one img {
    height: 170px;
}

.scramble-text {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    font-weight: 700;
    margin: 1.5rem 0 0 0;
    color: #fff;
}

.scramble-rating {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0.3rem 0 0 0;
    color: #fff;
}

.scramble-rating p {
    margin: 0;
}

.scramble-likes {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}

.scramble-likes img {
    width: 15px;
    height: 13px;
    margin-right: 6px;
}

.roadmap {
    width: 100%;
    height: 1000px;
    background-image: url(../src/assets/bg-2.png);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 1rem 0 4rem 0;
}

.roadmap-text-one {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    text-align: center;
}

.roadmap-text-one p {
    margin: 0;
}

.roadmap-progress {
    width: 100%;
    height: 850px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.roadmap-progress-one {
    width: 20%;
    height: 850px;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    color: #fff;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
}

.roadmap-progress-one img {
    width: 150px;
    align-self: center;
}

.roadmap-progress-two {
    width: 20%;
    height: 850px;
    /* border: 1px solid purple; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 2rem;
    text-align: center;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    color: #fff;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
}

.roadmap-progress-two img {
    width: 150px;
    align-self: center;
}

.roadmap-progress-three {
    width: 20%;
    height: 850px;
    /* border: 1px solid white; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 7rem;
    text-align: center;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    color: #fff;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
}

.roadmap-progress-three img {
    width: 150px;
    align-self: center;
}

.roadmap-progress-four {
    width: 20%;
    height: 850px;
    /* border: 1px solid yellow; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    color: #fff;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
}

.roadmap-progress-four img {
    width: 150px;
    align-self: center;
}

.roadmap-progress-responsive {
    display: none;
}

.roadmap-progress-res {
    padding-left: 2rem;
}

.roadmap-progress-res img {
    width: 17px;
    height: 17px;
    margin: 0 30px 0 10px;
}

.roadmap-progress-res p {
    margin: 0;
    color: #fff;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 20px;
    font-weight: bold;
}

.roadmap-progress-responsive-details-one {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}

.roadmap-progress-responsive-details-two {
    display: flex;
    flex-direction: row;
    margin-top: 3px;
}

.roadmap-text {
    display: flex;
    flex-direction: column;
    margin: 10px 10px 5px 24px;
}

.roadmap-text span {
    margin: 4px 0;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}

.border {
    width: 5px;
    height: 160px;
    background-image: linear-gradient(to right, #121117, #adaab9);
    margin: 0 10px 0 16px;
}

.borders {
    width: 5px;
    height: 100px;
    background-image: linear-gradient(to right, #121117, #adaab9);
    margin: 0 10px 0 16px;
}

.borderss {
    width: 5px;
    height: 130px;
    background-image: linear-gradient(to right, #121117, #adaab9);
    margin: 0 10px 0 16px;
}

.core-team {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(../src//assets/bg-1.png);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 3rem 0 5rem 0;
}

.core-team p {
    margin: 0;
}

.core-members {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 205px);
    column-gap: 30px;
    justify-content: center;
}

.core-team-text-one {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    padding-bottom: 1rem;
    text-align: center;
}

.core-team-text-two {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    color: #fff;
    padding: 1rem 1rem 4rem 1rem;
    text-align: center;
}

.core-member-one {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    margin-bottom: 3rem;
}

.core-member-img {
    width: 130px;
    border: 5px solid #704ba7;
    border-radius: 100px;
}

.core-member-img img {
    width: 120px;
    border-radius: 100px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 4px;
}

.core-member-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.core-member-text img {
    width: 25px;
    padding-bottom: 5px;
}

.core-member-text-one {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    font-weight: 700;
}

.core-member-text-two, .core-member-text-three {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 18px;
    padding: 0.3rem 0;
}

.advisors {
    padding: 0 0 3rem 0;
}

.advisors-text {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    text-align: center;
}

.advisors-details {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 4rem;
}

.advisors-details-one {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
    /* padding: 0 4rem; */
}

.advisors-details-one :nth-child(2) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    font-weight: 700;
    padding-top: 0.8rem;
}

.advisors-details-one :nth-child(3) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 18px;
    font-weight: 700;
    padding-top: 0.5rem;
}

.advisors-details-one :nth-child(4) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    --tw-text-opacity: 1;
    color: rgba(209, 213, 219, var(--tw-text-opacity));
    padding-top: 0.8rem;
    margin: 0 2.5rem;
    line-height: 25px;
}

.advisors-details-two {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
    /* padding: 0 4rem; */
}

.advisors-details-two :nth-child(2) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    font-weight: 700;
    padding-top: 0.8rem;
}

.advisors-details-two :nth-child(3) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 18px;
    font-weight: 700;
    padding-top: 0.5rem;
}

.advisors-details-two :nth-child(4) {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    --tw-text-opacity: 1;
    color: rgba(209, 213, 219, var(--tw-text-opacity));
    padding-top: 0.8rem;
    margin: 0 2.5rem;
    line-height: 25px;
}

.advisors-details-one img, .advisors-details-two img {
    width: 150px;
    border-radius: 100px;
}

.partners {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(../src/assets/partners.png);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 3rem 0;
    margin: 0 auto;
}

.partners-text-one {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    padding: 0 1rem;
}

.partners-text-two {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    color: #fff;
    text-align: center;
    padding: 1rem 1.5rem
}

.partners-text-one, .partners-text-two p {
    margin: 0;
}

.partners-group {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(5, minmax(150px, 160px));
    column-gap: 60px;
    justify-content: center;
    justify-items: center;
    align-items: center;
    padding: 2rem 0;
}

.partners-group img {
    width: 100%;
    max-width: 140px;
    margin-bottom: 3rem;
}

.x21 {
    width: 110px;
}

.gs {
    width: 110px;
}

.redhat {
    width: 110px;
}

.crypto {
    width: 110px;
}

.luaventures {
    width: 100px;
}

.footer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #121117;
    padding: 3rem 0;
}

.footer p {
    margin: 0 1rem;
}

.footer img {
    width: 80px;
}

.footer-text {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 30px;
    color: #fff;
    text-align: center;
}

.footer-text-two {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    color: #fff;
    text-align: center;
    padding-top: 2.5rem;
    padding-bottom: 0.5rem;
}

.footer-text-three {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    color: #fff;
    text-align: center;
}

.footer-icon {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    padding: 2rem 2rem 0 2rem;
}

.footer-icon img {
    width: 25px;
    margin: 0.5rem;
}