@media screen and (max-width: 1200px) {
    .partners-group {
        grid-template-columns: repeat(4, minmax(150px, 160px));
        column-gap: 40px;
    }
}

@media screen and (max-width: 992px) {
    .marketplace-items {
        grid-template-columns: repeat(3, minmax(180px, 279px));
    }
    .core-members {
        grid-template-columns: repeat(3, 205px);
    }
    .partners-group {
        grid-template-columns: repeat(4, minmax(150px, 160px));
        column-gap: 40px;
    }
}

@media screen and (max-width: 768px) {
    .navigation-menu {
        display: none;
    }
    .about-details {
        width: 100%;
        padding-bottom: 4rem;
    }
    .bike {
        display: none;
    }
    .demo-btn {
        margin-right: 5px;
    }
    .whitepaper-btn {
        width: 170px;
    }
    .features-details-text {
        flex-direction: column;
    }
    .features-details-one, .features-details-two, .features-details-three {
        margin: 1rem 2rem;
    }
    .marketplace-items {
        grid-template-columns: repeat(3, minmax(180px, 279px));
    }
    .roadmap {
        height: 800px;
    }
    .roadmap-progress {
        display: none;
    }
    .roadmap-progress-responsive {
        display: block;
    }
    .core-members {
        grid-template-columns: repeat(2, 205px);
        column-gap: 80px;
    }
    .advisors-details {
        flex-direction: column;
        align-items: center;
    }
    .advisors-details-one, .advisors-details-two {
        width: 100%;
        padding-top: 2rem;
        padding-bottom: 2rem;
        padding-left: 0;
        padding-right: 0;
    }
    .advisors-details-one :nth-child(4), .advisors-details-two :nth-child(4) {
        padding: 0 1.5rem;
    }
    .partners-group {
        grid-template-columns: repeat(3, minmax(150px, 160px));
        column-gap: 40px;
    }
}

@media screen and (max-width: 576px) {
    .logo {
        padding: 0;
    }
    .logo img {
        margin-left: 1.5rem;
    }
    .navigation-menu {
        display: none;
    }
    .about-details {
        width: 100%;
    }
    .bike {
        display: none;
    }
    .about-text-one, .about-text-two, .about-text-three {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    .home-btn {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    .demo-btn {
        width: 150px;
    }
    .whitepaper-btn {
        width: 150px;
    }
    .contract-address {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    .features-details-text {
        flex-direction: column;
    }
    .features-details-one, .features-details-two, .features-details-three {
        margin: 1rem 2rem;
    }
    .marketplace-items {
        grid-template-columns: repeat(2, minmax(180px, 279px));
    }
    .core-members {
        grid-template-columns: repeat(2, 205px);
        column-gap: 0;
    }
    .advisors-details {
        flex-direction: column;
        align-items: center;
    }
    .advisors-details-one, .advisors-details-two {
        width: 100%;
        padding-top: 2rem;
        padding-bottom: 2rem;
        padding-left: 0;
        padding-right: 0;
    }
    .advisors-details-one :nth-child(4), .advisors-details-two :nth-child(4) {
        padding: 0 1.5rem;
    }
    .partners-group {
        grid-template-columns: repeat(2, minmax(150px, 160px));
        column-gap: 40px;
    }
}

@media screen and (max-width: 375px) {
    .navigation-menu {
        display: none;
    }
    .about-details {
        width: 100%;
        padding-bottom: 4rem;
    }
    .logo {
        padding: 0;
    }
    .logo img {
        margin-left: 1.5rem;
    }
    .bike {
        display: none;
    }
    .about-text-one, .about-text-two, .about-text-three {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    .home-btn {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    .demo-btn {
        width: 105px;
    }
    .whitepaper-btn {
        width: 105px;
    }
    .contract-address {
        margin-left: 1.5rem;
    }
    .contract-address .tooltip {
        /* margin-top: 0.2rem; */
        margin-bottom: 0.3rem;
    }
    .marketplace-items {
        grid-template-columns: repeat(1, minmax(180px, 279px));
    }
    .core-members {
        grid-template-columns: repeat(1, 205px);
        column-gap: 10px;
    }
    .advisors-details-one, .advisors-details-two {
        padding-left: 0;
        padding-right: 0;
    }
    .partners-group {
        grid-template-columns: repeat(1, minmax(150px, 160px));
    }
}